.sort {
  padding: 12px 16px;
  display: flex;
  align-items: center;
  gap: 16px;
}

.sort__link {
  text-decoration: none;
  font-size: 18px;
  color: #00bfff;
  line-height: 20px;
  cursor: pointer;
}

.sort__link_selected {
  color: #ffffff;
  cursor: pointer;
}

.sort__link:hover {
  color: #ffffff;
  cursor: pointer;
}
